<template>
  <!-- 分类页面 -->
  <div class="category">
    <navbar class="nav-bar">
      <div slot="center">
        <h2>商品分类</h2>
      </div>
    </navbar>
    <div class="coodsList">
      <!-- 左侧导航 -->
      <categoryTab :list="categoryList" @selectItem="_clickAside"></categoryTab>
      <!-- 右侧内容 -->
      <scroll class="chid-content" ref="scroll">
       <categoryContent class="child-right" :goodsListItem="showSubcategory"></categoryContent>
       <tab-contro :titles="['综合', '新品', '销量']" @tabclick="_tabclick"></tab-contro>
       <goods :goodsall="showCategoryDetail"></goods>
      </scroll>
    </div>
  </div>
</template>

<script>
// 网络请求
import { getcategory, getSubcategory, getCategoryDetail} from 'network/category'
import navbar from 'components/common/navbar/navbar'
import categoryTab from './categoryItem/categoryTab'
import categoryContent from './categoryItem/categoryContent'
import tabContro from 'components/content/tabContro/tabContro'
import goods from 'components/content/goods/goods'
import scroll from 'components/common/scroll/scroll'
export default {
  name: 'MyMallCategory',

  data() {
    return {
      // 保存分类数据
      categoryList: [],
      categoryData: [],
      currentIndex: -1,
      // tab默认的请求数据是'pop',
      currentType: 'pop',
    }
  },
  components: {
    categoryTab,
    navbar,
    categoryContent,
    scroll,
    tabContro,
    goods
  },
  created() {
    this._getcategory()
  },
   activated() {
    this.$refs.scroll.scroll.refresh()
  },
  methods: {
    // 网络请求
    // 分类导航请求
    _getcategory() {
      getcategory().then(res => {
        // 保存侧边栏分类数据
        this.categoryList = res.data.category.list
        this.categoryList.forEach((item,i) => {

          this.categoryData[i] = {

            subcategories: {},
            categoryDetail: {
              'pop': [],
              'new': [],
              'sell': []
            }
          }

        })
        // 请求第一条分类数据
        this._getSubcategory(0)
      })
    },

      // 商品的分类请求
    _getSubcategory(index) {
      this.currentIndex = index
      let maitKey = this.categoryList[index].maitKey
      getSubcategory(maitKey).then(res => {
        // 将数据保存到分类导航定义的对象里面
        this.categoryData[index].subcategories = res.data
        this.categoryData = { ...this.categoryData }

        this._getCategoryDetail('pop')
        this._getCategoryDetail('new')
        this._getCategoryDetail('sell')
      })
    },

    // tab数据
    _getCategoryDetail(type) {
      // 1. 获取商品分类请求里的miniWallkey
      let miniWallkey = this.categoryList[this.currentIndex].miniWallkey
      // 2. 将类型type, 和获取到的参数miniwallkey传递给后台
      getCategoryDetail(miniWallkey, type).then(res => {
        // 3. 将数据保存到分类导航定义的对象熟悉categoryDetail里面
        this.categoryData[this.currentIndex].categoryDetail[type] = res
        this.categoryData = { ...this.categoryData }
      })
    },
    // 事件处理
    _clickAside(index) {
      this._getSubcategory(index)
    },
    _tabclick(index) {
      // 根据索引给this.currentType赋不同类型的值
      switch(index) {
        case 0: 
          this.currentType = 'pop'
          break
        case 1: 
          this.currentType = 'new'
          break
        case 2: 
          this.currentType = 'sell'
          break
      }
    } 
  },

  computed: {
    // 对商品分类数据进行处理
    showSubcategory() {
      if (this.currentIndex === -1) return {}
      return this.categoryData[this.currentIndex].subcategories
    },
    // 对tab数据进行处理
    showCategoryDetail() {
      if (this.currentIndex === -1) return [];
      return this.categoryData[this.currentIndex].categoryDetail[this.currentType]
    }
  }
}
</script>

<style scoped>
.category .nav-bar {
  position: relative;
  z-index: 99;
  background: var(--bgcolor);
}
.category .nav-bar h2 {
  font-size: 4.2667vw;
  font-weight: normal;
  color: #fff;
}
.category .coodsList {
  display: flex;
  height:100vh;
}
.chid-content {
  overflow: hidden;
  height: calc(100% - 11.7333vw - 13.0667vw);
}
.category .coodsList .child-right {
  flex: 1;
}

</style>