<template>
  <!-- 侧边栏 -->
  <div class="childcategoryItem">
    <scroll class="content-list" ref="scroll">
      <div
        class="list-title"
        :class="{ activeListItem: isIndex == index }"
        v-for="(item, index) in list"
        :key="index"
        @click="activeStyle(index)">
        <p>{{ item.title }}</p>
      </div>
    </scroll>
  </div>
</template>

<script>
import scroll from "components/common/scroll/scroll"
export default {
  data() {
    return {
      isIndex: 0,
    }
  },
  props: {
    list: {
      type: Array,
      default() {
        return []
      }
    }
  },
  components: {
    scroll,
  },
   activated() {
    this.$refs.scroll.scroll.refresh()
  },
  methods: {
    activeStyle(index) {
      this.isIndex = index
      this.$emit("selectItem", index)
    }
  },
  activated() {
    this.$refs.scroll.scroll.refresh()
  }
}
</script>

<style scoped>
.childcategoryItem {
  height: 100vh;
  background: #f6f6f6;
}
.childcategoryItem .content-list {
  /* overflow: hidden; */
  height: calc(100vh - 24.8vw);
}
.childcategoryItem .list-title {
  width: 26.6667vw;
  height: 12vw;
  line-height: 12vw;
  text-align: center;
  transition: all 0.3s linear;
  font-size: 3.7333vw;
  color: #333;
}
/* 活跃时 */
.activeListItem {
  color: var(--text-height-color) !important;
  background: #fff;
  border-left: 0.8vw solid var(--text-height-color);
  font-weight: 700;
}
</style>