<template>
  <!-- 这里只是文字颜色不一样用不着插槽，文字通过数组传递过来 -->
  <section class="tabContro">
    <ul>
      <li v-for="(item, index) in titles" :key="index" 
        @click="itemclick(index)">
        <span :class="{ active: index === activeindex }">{{ item }}</span>
      </li>
    </ul>
  </section>
</template>

<script>
export default {
  props: {
    titles: {
      type: Array,
      default() {
        return []
      }
    }
  },
  data() {
    return {
      // 记录状态
      activeindex: 0
    }
  },
  methods: {
    itemclick(index) {
      this.activeindex = index
      this.$emit('tabclick',index)
    }
  }
}
</script>

<style scoped>
.tabContro ul {
  display: flex;
  height: 10.6667vw;
  line-height: 10.6667vw;
  text-align: center;
  font-size: 4vw;
}
.tabContro ul li {
  flex: 1;
}
.active {
  color: var(--text-height-color);
}
.tabContro ul li span {
  padding: 1.6vw;
  transition: color 0.2s linear;
}
.tabContro ul li .active {
  border-bottom: 0.8vw solid var(--border-color);
}
</style>